@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@400;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body {
  background-image: url(/reactapptest-repository/static/media/bg-intro-desktop.059183ee.png);
  background-color: hsl(0, 100%, 74%);
  color: white;
  background-size: 1200px;
  min-height: 100%;
  max-width: 100%;
}

.main-container {
  border: none;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: repeat(2, 1fr);
  grid-template-areas: "grid-1 grid-2 grid-3";
  margin: auto;
  margin-left: 20px;
  height: 635px;
  width: auto;
  grid-column-gap: 50px;
  -webkit-column-gap: 50px;
          column-gap: 50px;
}

.grid-1 {
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  grid-template-rows: repeat(2, 1fr);
  border: none;
  grid-area: grid-1;
  width: 500px;
  height: 300px;
  position: relative;
  top: 175px;
  margin-left: 150px;
}

.h1 {
  font-family: "Poppins", sans-serif;
  font-size: 44px;
  line-height: 50px;
}

.p {
  font-family: "Poppins", sans-serif;
  font-size: 16px;
}

.grid-2 {
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  grid-template-rows: repeat(1, 1fr);
  border: none;
  grid-area: grid-2;
  height: 50px;
  width: 450px;
  position: relative;
  top: 90px;
  left: 0px;
  z-index: 2;
}

.try-it-btn {
  font-family: "Poppins", sans-serif;
  background-color: rgb(96, 85, 165);
  color: white;
  width: 460px;
  height: 50px;
  border: none;
  border-radius: 10px;
  box-shadow: 0px 7px 0px rgba(0, 0, 0, 0.151);
  cursor: pointer;
}

.free-trial-btn {
  background-color: hsl(154, 59%, 51%);
  height: 50px;
  border: none;
  border-radius: 10px;
  box-shadow: 0px 2px 0px hsl(154, 50%, 41%);
  color: white;
  font-family: "Poppins", sans-serif;
  font-weight: 700;
  width: 400px;
  margin-bottom: 10px;
  cursor: pointer;
}

.grid-3 {
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  grid-template-rows: repeat(6, 1fr);
  border: none;
  background-color: white;
  grid-area: grid-3;
  border-radius: 10px;
  position: relative;
  top: 160px;
  right: 500px;
  height: 330px;
  width: 400px;
  padding: 30px;
  box-shadow: 0px 7px 0px rgba(0, 0, 0, 0.151);
}

.input {
  display: grid;
  grid-template-rows: repeat(4, 1fr);
  color: gray;
  background-color: white;
  height: 40px;
  width: 390px;
  border: 1px solid rgba(128, 128, 128, 0.507);
  border-radius: 5px;
  font-family: "Poppins", sans-serif;
  font-size: 12px;
  font-weight: 400;
}

.p-2 {
  color: gray;
  font-family: "Poppins", sans-serif;
  font-size: 8px;
  text-align: center;
}

span {
  color: hsl(0, 100%, 74%);
  font-weight: 700;
}

.background {
  background-size: 1200px;
  min-height: 100%;
  max-width: 100%;
  z-index: -1;
}

